import { BsFillTelephoneFill, BsWhatsapp } from "react-icons/bs";
import { IoEarthSharp } from "react-icons/io5";
import { RiArrowDownSFill } from "react-icons/ri";
import { FaShoppingCart } from "react-icons/fa";
import { GiHamburgerMenu } from "react-icons/gi";
import { ImCross } from "react-icons/im";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import Weather from "./Weather/Weather";
import DropdownLanguage from "./DropdownLanguage";

function FirstNavbar({ menuValue, handleMenu }) {
  const [languageMenu, setLanguageMenu] = useState(false);
  const [windowY, setWindowY] = useState(0); // Bigger or smaller header depending on windowY

  window.addEventListener("scroll", () => {
    const w = window.scrollY;
    setWindowY(w);
  });

  return (
    <nav
      className={`${
        windowY === 0 ? "md:py-8 py-3.5" : "lg:py-2 py-1.5"
      } customFirstNav mx-auto lg:px-8 md:px-4 lg:max-w-6xl flex flex-1 justify-between items-center bg-firstNav`}
    >
      <Link to="/">
        <img
          src="https://www.barlantravel.com/images/logo-white.png"
          alt="Lausof.com"
          className={`${
            windowY === 0 ? "lg:w-44 md:w-36" : "lg:w-40 md:w-32"
          } customFirstNav smMd:w-28 w-20 pl-2 py-1`}
        />
      </Link>
      <ul className="flex md:gap-x-3 smMd:gap-x-1 items-center lg:text-md text-xs font-medium">
        <Weather />
        <li>
          <a
            href="tel: +543875380900"
            className="flex gap-x-1 md:mx-0 mx-1 items-center cursor-pointer"
          >
            <BsFillTelephoneFill className="md:text-xs" />
            <p className="md:block hidden">CALL US</p>
          </a>
        </li>
        <li>
          <a
            href="https://wa.me/+543875380900"
            target="_blank"
            className="flex gap-x-1 md:mx-0 mx-1 items-center cursor-pointer"
          >
            <BsWhatsapp className="md:text-base text-md" />
            <p className="md:block hidden">WHATSAPP</p>
          </a>
        </li>
        <li className="relative" onClick={() => setLanguageMenu(!languageMenu)}>
          <a
            href="#"
            className="customList flex gap-x-1 md:mx-0 mx-1 items-center cursor-pointer"
          >
            <IoEarthSharp className="md:text-base text-md sm:block hidden" />
            <p className="text-xs">English</p>
            <RiArrowDownSFill />
          </a>
          {languageMenu && <DropdownLanguage />}
        </li>
        <li className="flex gap-x-1 md:mx-0 mx-1 items-center cursor-pointer">
          <p className="text-xs">AR$</p>
        </li>
        <li className="flex gap-x-1 md:mx-0 mx-1 items-center cursor-pointer">
          <FaShoppingCart className="md:text-xs text-md" />
          <p className="md:block hidden">CART</p>
        </li>
        <li className="block md:hidden sm:w-11" onClick={() => handleMenu()}>
          {menuValue ? (
            <ImCross className="text-base mx-2 cursor-pointer" />
          ) : (
            <GiHamburgerMenu className="text-lg mx-2 cursor-pointer" />
          )}
        </li>
      </ul>
    </nav>
  );
}

export default FirstNavbar;
