import React, { useState } from "react";
import DropdownMenu from "./DropdownMenu";
import UseWindowSize from "../UseWindowSize";
import FirstNavbar from "./FirstNavbar";
import { Link } from "react-router-dom";

function Header() {
  const [innerWidth] = UseWindowSize();
  const [toggleMenu, setToggleMenu] = useState(false);

  const handleMenu = () => {
    setToggleMenu(() => !toggleMenu);
  };

  const secondNavbarLinks = [
    { title: "HOME", to: "/" },
    { title: "PACKAGES", to: "#" },
    { title: "ACTIVITIES", to: "#" },
    { title: "TRANSFERS", to: "#" },
    { title: "RENT A CAR", to: "#" },
    { title: "NEWS", to: "#" },
    { title: "FAQ", to: "#" },
    { title: "ABOUT US", to: "/About" },
  ];

  return (
    <>
      <header className="z-20 2xl:sticky sticky top-0 flex flex-col w-full mx-auto font-Poppins">
        <div className="relative w-full text-white bg-firstNav">
          <FirstNavbar menuValue={toggleMenu} handleMenu={handleMenu} />

          {innerWidth < 900 && toggleMenu && (
            <DropdownMenu links={secondNavbarLinks} />
          )}
        </div>
        <div className="w-full text-white customSecondNav">
          <nav className=" mx-auto lg:py-1.5 py-0.5 px-4 max-w-7xl md:max-w-5xl hidden md:flex justify-between items-center">
            <ul className="flex gap-x-2.5 text-tiny">
              {secondNavbarLinks.map((item, index) => {
                return (
                  <Link
                    to={item.to}
                    className="lg:px-3 transition duration-700 md:px-2 sm:px-1 py-2.5 hover:bg-secondNav"
                    key={index}
                  >
                    <li>{item.title}</li>
                  </Link>
                );
              })}
            </ul>
            <Link to="/Contact" className="">
              <li className="customContact transition duration-700 mx-6 list-none p-2.5 text-tiny hover:bg-secondNav">
                CONTACT
              </li>
            </Link>
          </nav>
        </div>
      </header>
    </>
  );
}

export default Header;
