import React, { Suspense, useEffect } from "react";
import { useGlobalContext } from "../../Context";
import WeatherModal from "./WeatherModal";

function Weather() {
  const { singleDay, setSingleDay, isModalOpen, openModal } =
    useGlobalContext();

  // ¿? Bug with the setState (setSingleDay) if i don't wrap all in an useEffect.
  useEffect(() => {
    try {
      if (singleDay === undefined) return;

      const {
        weather: [{ main, description, icon }] = "",
        feels_like: { day: feelsLike0 } = "",
        temp: { day: temperature0 } = "",
      } = singleDay;
      const weatherProperties = {
        main,
        description,
        icon,
        feelsLike0,
        temperature0,
      };
      setSingleDay(weatherProperties);
    } catch (error) {
      console.log(error);
    }
  }, [singleDay]);

  //Convert Kelvin to Celsius
  const kelvinToCelsius = (kelvin) => {
    return (kelvin - 273.15).toFixed(1);
  };
  const feelsLike1 = kelvinToCelsius(singleDay.feelsLike0);
  const temperature1 = kelvinToCelsius(singleDay.temperature0);

  // Disable scrolling on modal open
  function disableScroll() {
    document.body.style.overflow = "hidden";
  }
  function enableScroll() {
    document.body.style.overflow = "initial";
  }

  if (isModalOpen) {
    disableScroll();
  } else {
    enableScroll();
  }

  return (
    <Suspense fallback={"Loading..."}>
      <div
        className="grid grid-cols-2 w-max mr-3.5 items-center justify-center cursor-pointer"
        onClick={openModal}
      >
        <img
          src={`http://openweathermap.org/img/w/${singleDay.icon}.png`}
          alt={singleDay.main}
          className="justify-self-end flex flex-shrink lg:mr-4 md:mr-2 "
        />
        <div className="flex flex-col items-center gap-y-1 lg:text-sm sm:text-xs text-10px">
          <h5 className="capitalize">{singleDay.description}</h5>
          <h6 className="text-center">{`${temperature1}°C | ST: ${feelsLike1}°C `}</h6>
        </div>
      </div>
      {/* Weather Modal */}
      {isModalOpen && <WeatherModal />}
    </Suspense>
  );
}

export default Weather;
