import React, { useEffect, useState } from "react";
import SinglePlace from "../SinglePlace"; // Single Place page
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import { setSingleData } from "../../components/redux/placeData";
import { Link } from "react-router-dom";
import star from "./images/star.png";

function ShowPlace({ kinds, name, rate, xid, fetchPlaces }) {
  const { singleData } = useSelector((state) => state.handleDataPlace);
  const dispatch = useDispatch();

  // const [singleData, setSingleData] = useState({
  //   preview: { source: undefined },
  //   wikipedia_extracts: { html: "" },
  // });

  // Random Math number (Price in USD$)
  function randomIntFromInterval(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }
  const rndInt = randomIntFromInterval(43, 79);

  useEffect(() => {
    fetchPlaces("xid/" + xid).then((item) => {
      dispatch(setSingleData(item));
    });
  }, []);

  console.log(singleData);

  return (
    <>
      <Link to={`/SinglePlace/${xid}`} className="custom-div">
        <img
          className="custom-image"
          style={{
            backgroundImage: `url(${singleData.preview.source})`,
          }}
        />
        <h2 className="custom-title">{name}</h2>
        <div className="custom-subtitle">
          <div className="custom-images">
            <img src={star} className="w-5" />
            <img src={star} className="w-5" />
            <img src={star} className="w-5" />
            {/* {console.log(singleData.wikipedia_extracts.html)} */}
          </div>
          <p className="custom-paragraph">USD$ {rndInt}</p>
        </div>
      </Link>
    </>
  );
}

export default ShowPlace;
