import React from "react";
import ReactDOM from "react-dom";
import "bootstrap/dist/css/bootstrap.min.css"; // Slider
import "./index.css";
import App from "./App";
import { AppProvider } from "./components/Context"; // React context
import { BrowserRouter } from "react-router-dom"; // React Router
import { Provider } from "react-redux"; // Redux
import store from "./components/redux/store";

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <AppProvider>
        <BrowserRouter>
          <App />
        </BrowserRouter>
      </AppProvider>
    </Provider>
  </React.StrictMode>,
  document.getElementById("root")
);
