import React, { useState, useEffect } from "react";
import { setPlacesData } from "../../components/redux/placeData";
import { useDispatch, useSelector } from "react-redux";
import LoadList from "./LoadPlacesList";

// Featured trips
function SecondSection() {
  const { placesData } = useSelector((state) => state.handleDataPlace);
  const dispatch = useDispatch();

  // const [placesData, setPlacesData] = useState([]);

  const API_KEY = "5ae2e3f221c38a28845f05b6b8f2401970a6fb70ad345257a406ee6e";
  const pageLength = 6; // number of objects per page
  let lon; // place longitude
  let lat; // place latitude
  let offset = 0; // offset from first object in the list

  // Get Places in Seattle
  function fetchPlaces(method, query) {
    return new Promise((resolve, reject) => {
      let URL = `https://api.opentripmap.com/0.1/en/places/${method}?apikey=${API_KEY}`;
      if (query !== undefined) {
        URL += "&" + query;
      }
      fetch(URL)
        .then((response) => response.json())
        .then((data) => resolve(data))
        .catch((err) => {
          reject(new Error("Fetch Error :-S", err));
        });
    });
  }

  useEffect(() => {
    //This block uses the placename from input textbox and gets place location from API. If place was found it calls list loading function:
    fetchPlaces("geoname", "name=Seattle").then((data) => {
      if (data.status === "OK") {
        lon = data.lon;
        lat = data.lat;
        loadList();
      }
    });

    //This function load list page to the left pane. It uses 1000 meters radius for objects search:
    function loadList() {
      fetchPlaces(
        "radius",
        `radius=1000&limit=${pageLength}&offset=${offset}&lon=${lon}&lat=${lat}&rate=2&format=json`
      ).then((data) => {
        dispatch(setPlacesData(data));
      });
    }
  }, []);

  return (
    <section className="lg:max-w-7xl mx-auto">
      <h1 className="pseudoClassesH1 lg:text-2xl text-xl my-4">
        Featured Trips
      </h1>
      <h2 className="text-lg text-center text-textGray font-Poppins mb-3">
        We present the best experiences to enjoy and get to know Bariloche
      </h2>
      <p id="info"></p>
      <div
        id="list"
        className="grid lg:grid-cols-3 smMd:grid-cols-2 grid-cols-1 justify-around"
      >
        {placesData.map((place) => {
          return (
            <LoadList key={place.xid} {...place} fetchPlaces={fetchPlaces} />
          );
        })}
      </div>
      <button id="next_button">See more places...</button>
    </section>
  );
}
export default SecondSection;
