import { GrFormClose } from "react-icons/gr";
import React, { useEffect } from "react";
import ReactDom from "react-dom";
import { useGlobalContext } from "../../Context";
import SingleWeather from "./SingleWeather";

function WeatherModal() {
  const { restOfDays, setRestOfDays, closeModal } = useGlobalContext();

  useEffect(() => {
    try {
      let {
        dt: dateTime = "",
        weather: [{ main, description, icon }] = [{}],
        temp: { max, min } = {},
        speed: windSpeed = "",
        humidity = "",
      } = (restOfDays = {});
      let weatherProperties = {
        dateTime,
        main,
        description,
        icon,
        max,
        min,
        windSpeed,
        humidity,
      };
      setRestOfDays(weatherProperties);
    } catch (error) {
      console.log(error);
    }
  }, [restOfDays]);

  return ReactDom.createPortal(
    <>
      <div className="absolute z-20 top-0 left-0 bottom-0 right-0 bg-modalBgWindow opacity-80"></div>
      <div className="customModal z-20 fixed md:w-8/12  max-w-xl mx-auto left-0 right-0 top-12 flex flex-col items-center justify-between shadow-xl bg-modalBg">
        <header className="w-full h-auto py-4 flex justify-between items-center bg-white text-titleColor font-semibold">
          <h2 className="text-base tracking-tight ml-2.5">
            The weather in Bariloche
          </h2>
          <GrFormClose
            className="text-blue-500 text-lg mr-3.5 text-left opacity-30 cursor-pointer"
            onClick={closeModal}
          />
        </header>
        <main className="w-11/12 py-2.5 flex flex-col bg-modalBg">
          {restOfDays.map((singleDay) => {
            return <SingleWeather key={singleDay.dt} {...singleDay} />;
          })}
        </main>
        <footer className="w-full h-auto bg-white text-right py-4">
          <button
            className="customModalButton border border-transparent bg-firstNav text-base py-1.5 px-5 mr-3.5  text-white tracking-widest rounded font-light"
            onClick={closeModal}
          >
            Accept
          </button>
        </footer>
      </div>
    </>,
    document.getElementById("modal")
  );
}

export default WeatherModal;
