//     case 'ENGLISH':
//       return {...state, language: action.payload.language}
//   }
// };

const AppContext = React.createContext();

function AppProvider({ children }) {
  // const [state, dispatch] = useReducer(reducer, initialStates);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [singleDay, setSingleDay] = useState({});
  const [restOfDays, setRestOfDays] = useState([{}]);

  // Search weather info, from today to 5 days forward.
  const CITY = "California";
  const DAYS_FORWARD = 5;
  const API_KEY_WEATHER = "b1b15e88fa797225412429c1c50c122a1";
  const URL = `https://pro.openweathermap.org/data/2.5/forecast/daily?q=${CITY}&cnt=${DAYS_FORWARD}&appid=${API_KEY_WEATHER}`;

  const fetchWeather = async () => {
    try {
      const response = await fetch(URL);
      const data = await response.json();
      if (data) {
        const weatherList = data.list;
        // Get first day
        const getSingleDay = weatherList[0];
        setSingleDay(getSingleDay);

        // Get all days
        setRestOfDays(weatherList);
      } else {
        new Error("Information can't be provided");
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    fetchWeather();
  }, [CITY]);

  // Open or close weather modal
  const openModal = () => {
    setIsModalOpen(true);
  };
  const closeModal = () => {
    setIsModalOpen(false);
  };

  // Set language
  // const handleLanguage = (language) => {
  //   dispatch({type: 'SET_LANGUAGE', payload: language })
  // }

  // Get Places in California
  const API_KEY = "5ae2e3f221c38a28845f05b6b8f2401970a6fb70ad345257a406ee6e";

  function fetchPlaces(method, query) {
    return new Promise((resolve, reject) => {
      let URL = `https://api.opentripmap.com/0.1/en/places/${method}?apikey=${API_KEY}`;
      if (query !== undefined) {
        URL += "&" + query;
      }
      fetch(URL)
        .then((response) => response.json())
        .then((data) => resolve(data))
        .catch((err) => {
          reject(new Error("Fetch Error :-S", err));
        });
    });
  }

  return (
    <AppContext.Provider
      value={{
        singleDay,
        setSingleDay,
        restOfDays,
        setRestOfDays,
        isModalOpen,
        openModal,
        closeModal,
        fetchPlaces,
      }}
    >
      {children}
    </AppContext.Provider>
  );
}

export const useGlobalContext = () => {
  return React.useContext(AppContext);
};

export { AppProvider, AppContext };
